.goods{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card{
  display: flex;
  flex-direction: column;
}

.card-content{
  flex-grow: 1;
}

.cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  cursor: pointer;
  z-index: 5;
  padding: 1rem;
  border-radius: 10px;
}

.absolute{
  width: 50rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  animation: show 500ms ease-in-out;
  

}
.point{
  cursor: pointer;
  display: flex;
  justify-content: space-between;
}

/* body :not(#absolute){
  filter:blur(2px)
} */


.margin{
  vertical-align: middle;
  margin: 0 5px;
  cursor: pointer;
  color: #26a69a;

}


@keyframes show {
  from{
    top: 70%;
    opacity: 0;
  }
  to{
    top: 50%;
    opacity: 1;
  }
}
@media(min-width: 767px){
  .cart{
    top: 5rem;
    bottom: unset;
  }
}